*,
*::before,
*::after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-width: 320px;
  max-width: 1920px;
  min-height: 100vh;
  font-family: "Montserrat", "arial", sans-serif;
  font-style: normal;
  background-color: #e5e5e5;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden,
input[type="file"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  clip: rect(0, 0, 0, 0);
  border: 0;
  overflow: hidden;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff2") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.button {
  position: relative;
  display: inline-block;
  border: none;
  cursor: pointer;
  font-family: inherit;
}
.page {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  flex-direction: column;
  min-height: 100vh;
}
.page-header {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-bottom: 194px;
  padding: 35px 7.54% 0;
}
.page-header__logo {
  margin-right: auto;
  color: #F9FBFD;
}
.page-header__toggle {
  display: none;
  margin: 0;
  padding: 0;
  border: 0;
}
@media (max-width: 1023px) {
  .page-header {
    margin-bottom: 214px;
    padding: 30px 45px 0;
  }
}
@media (max-width: 767px) {
  .page-header {
    margin-bottom: 130px;
    position: relative;
    padding: 20px 15px 0;
  }
  .page-header--nojs {
    padding: 0;
  }
  .page-header .page-header__logo {
    color: #F9FBFD;
  }
  .page-header--nojs .page-header__logo {
    position: absolute;
    top: 20px;
    left: 15px;
    color: #011C40;
  }
  .page-header--menu-open .page-header__logo {
    color: #011C40;
  }
  .page-header--nojs .page-header__nav-list {
    display: block;
    position: static;
    width: 100%;
  }
  .page-header--menu-open .page-header__nav-list {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }
  .page-header .page-header__toggle {
    display: block;
    width: 24px;
    height: 12px;
    z-index: 10;
    background-image: url("../img/menu.svg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 24px 12px;
    background-color: inherit;
  }
  .page-header .page-header__toggle:hover {
    cursor: pointer;
    opacity: 0.5;
  }
  .page-header--nojs .page-header__toggle {
    display: none;
  }
  .page-header--menu-open .page-header__toggle {
    display: block;
    width: 13px;
    height: 13px;
    background-image: url("../img/close.svg");
    background-size: 13px 13px;
  }
}
.logo {
  z-index: 10;
}
.logo__image {
  width: 70px;
  height: 38px;
}
@media (max-width: 1023px) {
  .logo__image {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo__image {
    width: 42px;
    height: 22px;
  }
}
.main-nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 80%;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item:not(:last-child) {
  margin-right: 33px;
}
.main-nav__link {
  color: #F9FBFD;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
}
.main-nav__link:hover,
.main-nav__link:focus {
  cursor: pointer;
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .main-nav__item:not(:last-child) {
    margin-right: 23px;
  }
  .main-nav__link {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.08px;
  }
}
@media (max-width: 767px) {
  .main-nav {
    display: none;
    flex-direction: column;
    padding: 93px 15px 65px;
    color: #011C40;
    background-color: #F9FBFD;
    max-width: 100%;
  }
  .main-nav--footer {
    display: flex;
    margin: 0;
    padding: 0;
    margin-bottom: 30px;
    background-color: inherit;
  }
  .main-nav__item:not(:last-child) {
    margin-left: 0;
    margin-bottom: 30px;
  }
  .main-nav__item--footer:not(:last-child) {
    margin-bottom: 23px;
  }
  .main-nav__link {
    font-size: 18px;
    line-height: 22px;
    color: #011C40;
  }
  .main-nav__link--footer {
    font-size: 12px;
    line-height: 15px;
    color: #F9FBFD;
  }
}
.page-main {
  flex-grow: 1;
}
.page-label {
  color: #F9FBFD;
  padding-left: 14.5%;
}
.page-label__image {
  z-index: -1;
  position: absolute;
  display: block;
  width: 100%;
  height: 650px;
  top: 0;
  left: 0;
  right: 0;
  background-color: #2F80ED;
}
.page-label__title {
  z-index: 10;
  margin: 0;
  padding: 0;
  margin-bottom: 12px;
  font-weight: bold;
  font-size: 54px;
  line-height: 66px;
}
.page-label__slogan {
  z-index: 10;
  margin: 0;
  padding: 0;
  margin-bottom: 277px;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .page-label {
    padding-left: 5.86%;
  }
  .page-label__image {
    height: 638px;
  }
  .page-label__title {
    margin-bottom: 12px;
    font-size: 50px;
    line-height: 61px;
  }
  .page-label__slogan {
    margin-bottom: 276px;
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .page-label {
    padding-left: 4.69%;
  }
  .page-label__image {
    height: 500px;
  }
  .page-label__title {
    margin-bottom: 12px;
    font-size: 36px;
    line-height: 45px;
  }
  .page-label__slogan {
    margin-bottom: 178px;
    font-size: 16px;
    line-height: 22px;
  }
}
.voyage {
  display: flex;
  align-items: center;
  padding: 120px 7.54% 117px;
  color: #011C40;
  background-color: #F9FBFD;
}
.voyage__wrapper {
  display: flex;
  flex-direction: column;
  margin-right: auto;
}
.voyage__title {
  margin: 0;
  padding: 0;
  margin-bottom: 25px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
.voyage__description {
  width: 88%;
  margin: 0;
  padding: 0;
  padding-bottom: 8px;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}
.voyage__image {
  max-width: none;
  width: 560px;
  height: 301px;
  flex-shrink: 0;
}
@media (max-width: 1023px) {
  .voyage {
    padding: 89px 5.86% 93px;
  }
  .voyage__title {
    width: 70%;
    margin-bottom: 18px;
    font-size: 26px;
    line-height: 36px;
  }
  .voyage__description {
    padding: 0;
    width: 91%;
  }
  .voyage__image {
    width: 324px;
    height: 264px;
  }
}
@media (max-width: 767px) {
  .voyage {
    flex-direction: column;
    padding: 57px 15px 62px;
  }
  .voyage__wrapper {
    margin: 0;
    padding: 0;
    margin-bottom: 33px;
  }
  .voyage__title {
    margin-bottom: 15px;
    font-size: 24px;
    line-height: 34px;
  }
  .voyage__description {
    width: 100%;
  }
  .voyage__image {
    width: 290px;
    height: 186px;
  }
}
.advantage {
  padding: 110px 103px 118px;
  background: #e3ecf6;
}
.advantage__title {
  margin: 0;
  padding: 0;
  margin-bottom: 48px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
.advantage__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}
.advantage__item {
  position: relative;
  width: 31%;
  padding: 82px 30px 26px;
  background-color: #F9FBFD;
}
.advantage__item::before {
  position: absolute;
  content: "";
  left: 30px;
  background-repeat: no-repeat;
  background-position: center;
}
.advantage__item--ship::before {
  top: 30px;
  width: 27px;
  height: 32px;
  background-image: url("../img/advantage-ship.svg");
  background-size: 27px 32px;
}
.advantage__item--include::before {
  top: 35px;
  width: 38px;
  height: 21px;
  background-image: url("../img/advantage-include.svg");
  background-size: 38px 21px;
}
.advantage__item--zodiac::before {
  top: 38px;
  width: 42px;
  height: 15px;
  background-image: url("../img/advantage-zodiac.svg");
  background-size: 42px 15px;
}
.advantage__name {
  margin: 0;
  padding: 0;
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  text-align: left;
}
.advantage__content {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
}
@media (max-width: 1023px) {
  .advantage {
    padding: 90px 45px;
  }
  .advantage__title {
    margin-bottom: 48px;
    font-size: 26px;
    line-height: 39px;
  }
  .advantage__item {
    width: 30.38%;
    padding: 72px 18px 22px;
    background-color: #F9FBFD;
  }
  .advantage__item::before {
    position: absolute;
    content: "";
    left: 20px;
    background-repeat: no-repeat;
    background-position: center;
  }
  .advantage__item--ship::before {
    top: 25px;
    width: 21px;
    height: 26px;
    background-image: url("../img/advantage-ship.svg");
    background-size: 21px 26px;
  }
  .advantage__item--include::before {
    top: 29px;
    width: 33px;
    height: 19px;
    background-image: url("../img/advantage-include.svg");
    background-size: 33px 19px;
  }
  .advantage__item--zodiac::before {
    top: 32px;
    width: 38px;
    height: 13px;
    background-image: url("../img/advantage-zodiac.svg");
    background-size: 38px 13px;
  }
  .advantage__name {
    line-height: 25px;
  }
}
@media (max-width: 767px) {
  .advantage {
    padding: 56px 15px;
  }
  .advantage__title {
    margin-bottom: 30px;
    font-size: 24px;
    line-height: 36px;
  }
  .advantage__list {
    flex-direction: column;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .advantage__item {
    width: 100%;
    padding: 71px 20px 20px;
    background-color: #F9FBFD;
  }
  .advantage__item:not(:last-child) {
    margin-bottom: 31px;
  }
  .advantage__item::before {
    position: absolute;
    content: "";
    left: 20px;
    background-repeat: no-repeat;
    background-position: center;
  }
  .advantage__item--ship::before {
    top: 25px;
    width: 21px;
    height: 26px;
    background-image: url("../img/advantage-ship.svg");
    background-size: 21px 26px;
  }
  .advantage__item--include::before {
    top: 29px;
    width: 33px;
    height: 19px;
    background-image: url("../img/advantage-include.svg");
    background-size: 33px 19px;
  }
  .advantage__item--zodiac::before {
    top: 32px;
    width: 38px;
    height: 13px;
    background-image: url("../img/advantage-zodiac.svg");
    background-size: 38px 13px;
  }
  .advantage__item .advantage__name {
    width: 70%;
    margin-bottom: 8px;
  }
}
.page-footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-shrink: 0;
  margin: 0;
  padding: 0;
  padding: 50px 7.54%;
  background-color: #011C40;
}
.page-footer__logo {
  margin-right: 40px;
  color: #F9FBFD;
}
.page-footer__social {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .page-footer {
    padding: 50px 45px;
  }
  .page-footer__logo {
    margin-right: 25px;
  }
}
@media (max-width: 767px) {
  .page-footer {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 40px 15px;
  }
  .page-footer__logo {
    margin: 0;
    padding: 0;
    margin-bottom: 30px;
    color: #F9FBFD;
  }
  .page-footer__social {
    justify-content: flex-start;
  }
}
.social {
  margin: 0;
  padding: 0;
  margin-left: auto;
  list-style: none;
  min-width: 14%;
}
.social__item:not(:last-child) {
  margin-right: 33px;
}
.social__link:hover,
.social__link:focus {
  cursor: pointer;
  opacity: 0.5;
}
.social__link-image {
  color: #F9FBFD;
}
@media (max-width: 1023px) {
  .social__item:not(:last-child) {
