  }
  .social__link-image--instagram {
    width: 16px;
    height: 16px;
  }
  .social__link-image--twitter {
    width: 16px;
    height: 13px;
  }
}
@media (max-width: 767px) {
  .social {
    margin: 0;
    padding: 0;
  }
  .social__link-image--facebook {
    width: 9px;
    height: 16px;
  }
  .social__link-image--instagram {
    width: 16px;
    height: 16px;
  }
  .social__link-image--twitter {
    width: 16px;
    height: 13px;
  }
}
